//
// forms.scss
//

.form-control {
    transition: color 0.25s, border-color 0.25s;
    line-height: 1;
    padding: 0 12px;

    &::placeholder {
        color: #758796;
    }

    &:hover, &:focus {
        border-color: #0000FF !important;
    }

    &:placeholder-shown {
        border-color: #A0B1BA;
    }
}

.form-control,
.custom-select {
    box-shadow: $components-shadow-sm;
}

// Form-control light
.form-control-light {
    background-color: $input-light-bg !important;
    border-color: $input-light-bg !important;
}


// Form elements (Color and Range)
input.form-control[type="color"],
input.form-control[type="range"] {
    min-height: 39px;
}


// Custom select
.custom-select.is-invalid, .form-control.is-invalid,.custom-select:invalid, .form-control:invalid,
.custom-select.is-valid, .form-control.is-valid, .custom-select:valid,.form-control:valid {
    &:focus {
        box-shadow: none !important;
    }
}

// Comment box
.comment-area-box {
    .form-control {
        border-color: $gray-300;
        border-radius: $input-border-radius $input-border-radius 0 0;
    }
    .comment-area-btn {
        background-color: $gray-100;
        padding: 10px;
        border: 1px solid $gray-300;
        border-top: none;
        border-radius: 0 0 $input-border-radius $input-border-radius;
    }
}

// Search box 
.search-bar {

    .form-control {
        padding-left: 40px;
        padding-right: 20px;
        border-radius: 30px;
    }

    span {
        position: absolute;
        z-index: 10;
        font-size: 16px;
        line-height: $input-height;
        left: 13px;
        top: -2px;
        color: $text-muted;
    }
}

// Show Password
.password-eye {
    &:before {
        font-family: feather!important;
        content: "\e86a";
        font-style: normal;
        font-weight: 400;
        font-variant: normal;
        vertical-align: middle;
        line-height: 1.2;
        font-size: 16px;
    }
}

.show-password {
    .password-eye {
        &:before {
            content: "\e86e";
        }
    }  
}

.form-check-inline {
    white-space: nowrap;
}

select.form-control {
    appearance: none;

    &:not(:focus):invalid {
        color: #758796;
        border-color: #a0b1ba;
    }
}

.form-control__wrap._select {
    position: relative;
    &:after {
        content: '';
        display: block;
        width: 8px;
        height: 4.94px;
        background-image: url(../images/dropdown-arrow.svg);
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        position: absolute;
        right: 10px;
        top: 12px;
    }
}

// Validation error styles
.input-validation-error {
    border-color: #ED1C24 !important;
}

.invalid-feedback, .field-validation-error {
    color: #ED1C24 !important;
    font-size: 12px;
    font-weight: 600;
    margin-top: 7px;

    &:before {
        content: '';
        display: inline-block;
        width: 8.75px;
        height: 10px;
        background-image: url("../images/form-validation-icon.svg");
        background-repeat: no-repeat;
        background-position: center;
        margin-right: 2px;
    }

    .checkbox & {
        transform: translate(-16px, -2px);
    }
}

.field-validation-error {
    display: block;

    &:before {
        margin-right: 4px;
    }
}

.radio {
    &_inline {
        position: relative;
        white-space: nowrap;
        display: inline-flex;
        margin-right: 19px;

        label {
            padding-left: 8px;
            &:before, &:after {
                margin-top: -1px;
            }
        }

        .invalid-feedback,
        .field-validation-error {
            position: absolute;
            top: 15px;
            left: 0;
        }
    }
}

.was-validated .radio :invalid + label,
.radio .input-validation-error + label {
    margin-bottom: 18px;
}

.form-control.is-valid, .was-validated .form-control:valid, .was-validated .form-control:valid:focus {
    border-color: #a0b1ba;
    background-image: none;
}

.form-control.is-invalid, .was-validated .form-control:invalid {
    background-image: none;
}

.form-control:not(.input-validation-error) + .field-validation-error,
form input[type="radio"]:not(.input-validation-error) ~ .field-validation-error,
form input[type="checkbox"]:not(.input-validation-error) ~ .field-validation-error {
    display: none;
}