// 
// nav.scss
//

.nav-tabs,.nav-pills {
    > li {
        > a {
            color: #212B35;
            font-weight: 500;
        }
    }
}

.nav-pills {
    > a {
        color: $gray-700;
        font-weight: $label-font-weight;   
    }
}

.nav-tabs {
    .nav-item {
        line-height: 19px;
        font-size: 14px;
        margin-bottom: 10px;

        li {
            a {
                &.active {
                    cursor: default;
                    pointer-events: none;
                }
            }
        }
    }
}


//Navtab custom
.navtab-bg {
    .nav-link {
        background-color: $card-cap-bg;
        margin: 0 5px;
    }
}


// Nav bordered

.nav-bordered {
    border-bottom: 2px solid transparent !important;

    li {
        a {
            border-width: 0 0 4px 0 !important;
            border-style: solid !important;
            border-color: transparent !important;
            padding: 10px 20px 6px;
            transition: 0.25s;

            &:hover {
                border-color: #B6FFE8 !important;
                color: #0000FF;
            }

            &.active {
                border-width: 0 0 4px 0 !important;
                border-color: #00eba8 !important;
            }
        }
    }

a.active {
    border-bottom: 2px solid $primary !important;
}
}

.tab-content {
    position: relative;
    padding: 18px 0 0 0;
}