// Font Weight

.font-weight-medium {
    font-weight: $font-weight-medium !important;
}

.font-weight-semibold {
    font-weight: $font-weight-semibold !important;
}

// Line Height

.lh-1-3 {
    line-height: 1.33333;
}

.lh-1 {
    line-height: 1;
}

// Color
.color-primary {
    color: #212b35;
}

.color-secondary {
    color: #454F5B;
}

.color-black {
    color: #000;
}

.color-danger {
    color: #ED1C24;
}